import './MessengerChatForm.css'
import IMAGES from '../../images'
import {useRef} from "react";
import {useDispatch, useSelector} from "react-redux";
import {selectMessage, sendMessage} from "../../store/slices/messenger/messengerSlices";

function MessengerChatForm() {
	const formRef=useRef(null)
	const dispatch=useDispatch()

	const handelSubmit=(e)=>{
		e.preventDefault()
		const value = formRef.current[0].value
		// console.log(value)
		if (value.length){
			dispatch(sendMessage(value))
			formRef.current.reset()
		}
	}

  return (
	 <form  onSubmit={handelSubmit} ref={formRef} className='Chat-input'>
		<input type='text'  placeholder='Message...'/>
		<img src={IMAGES.like} alt=''/>
	 </form>
  )
}

export default MessengerChatForm
