import {useNavigate} from "react-router-dom";
import "./ErrorPage.css"

function ErrorPage () {
    const navigate = useNavigate();
    const backToHomePage = () => {
        navigate('/');
    }
    return (
        <div className="errbody">
            <div className="err">
                <h1 className="errtitle">
                    Error 404
                </h1>
                <p className="errtext">The page is not found</p>
                <button onClick={backToHomePage} className="backtohome" >
                    Back to Home
                </button>
            </div>
        </div>

    )
}

export default ErrorPage