import './MessengerPeoplesMessage.css'
import {useDispatch} from "react-redux";
import {getterUserID} from "../../store/slices/messenger/messengerSlices";

function MessengerPeoplesMessage({name,active,img,id}) {

	const dispatch=useDispatch()
  return (
	 <div style={{cursor:"pointer"}} onClick={()=>dispatch(getterUserID(id))} className='Messenger-left-col-people-message'>
		<div className='Messsage-img'>
			<img src={img} alt=''/>
		</div>
		<div  className='Message-info'>
			<p>{name}</p>
			<p>{active}</p>
		</div>
	 </div>
  )
}

export default MessengerPeoplesMessage
