import {createSlice} from "@reduxjs/toolkit";
import {fetchPosts} from "./postsAPI";

const postsSlice = createSlice({
    name: "posts",
    initialState: {
        isLoading: false,
        postsData: []
    },
    reducers: {
        addComments(state, {payload: {id, username, body}}) {
            const index = state.postsData.findIndex(post => post.id === id)
            state.postsData[index].comments.push({
                id: new Date().getTime().toString(),
                username, body
            })
        },
        addPost(state,{payload}){
            state.postsData.unshift({...payload})
        },
        deletePost (state,{payload}){
            state.postsData=[...state.postsData.filter(el=>el.id!=={payload})]
        }
    },
    extraReducers: {
        [fetchPosts.pending]: (state) => {
            return (
                {
                    ...state,
                    isLoading: true
                }
            )


        },
        [fetchPosts.fulfilled]: (state, {payload}) => {
            if (state.isLoading) {
                return {
                    ...state,
                    postsData: [...payload],
                    isLoading: false
                }
            }


        },
        [fetchPosts.rejected]: () => {
            alert("Something is wrong")

        }
    }

})

export const selectPosts = state => state.posts

export const {addComments,addPost,deletePost} = postsSlice.actions

export const postsReducer = postsSlice.reducer

