import {createAsyncThunk} from "@reduxjs/toolkit";
import axios from "axios";

const fetchUsers=createAsyncThunk(
    "users/fetchUsers",
    async function (){
        const {data:usersData}=await axios.get("https://jsonplaceholder.typicode.com/users")
        const {data:photosData}=await axios.get("https://jsonplaceholder.typicode.com/photos?_limit=500")
        const data=usersData.map(user=>({
            id:user.id,
            name:user.name,
            username:user.username.toLowerCase(),
            email:user.email.toLowerCase(),
            password:user.address.city.toLowerCase(),
            avatar:`data:image/png;base64,`,
            followers:Math.round(Math.random()*100 +287),
            following:Math.round(Math.random()*100 +287),
            bio:user.company.catchPhrase.toLowerCase(),
            posts:[
                ...photosData.filter(post=>post.albumId===user.id)
                    .map(post=>({
                        id: post.id.toString()+ "_" + user.username.toLowerCase() ,
                        name: user.username.toLowerCase(),
                        likesCount: Math.round(Math.random() * 100 + 300),
                        timeAgo: Math.round(Math.random() * 3 + 4) + " Minutes Ago",
                        postText: post.title.split(" ").slice(1).join(" "),
                        img: post.url,
                        comments:[]
                    }))
            ]

        }))
        console.log(data)
        return data
    }
)

export default fetchUsers