
import React, {useEffect} from 'react'
import {Formik} from 'formik'
import * as yup from 'yup'
import {useNavigate} from 'react-router-dom'
import './Login.css'
import images from "../../images";
import {useDispatch, useSelector} from "react-redux";
import {logIn, selectUsers} from "../../store/slices/users/usersSlice";
import fetchUsers from "../../store/slices/users/usersAPI";


function Login() {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const {usersData, currentUser} = useSelector(selectUsers)

    useEffect(() => {
        console.log(currentUser)
        if (currentUser) {
            navigate("/")
        }
    }, [currentUser])

    useEffect(() => {
        if (!usersData.length) {
            dispatch(fetchUsers())
        }
    }, [])

    const validationSchema = yup.object().shape({
        email: yup.string().typeError('Must be a string').required('Required field'),
        password: yup.string().typeError('Must be a string').min(3, 'Very short password')
    })
    return (
        <Formik
            initialValues={{
                email: 'bret',
                password: 'gwenborough'
            }}

            onSubmit={(value, {resetForm}) => {
                console.log(value)
                dispatch(logIn(value))
                resetForm()
            }}

            validateOnBlur

            validationSchema={validationSchema}

        >

            {({values, errors, touched, handleChange, handleBlur, isValid, handleSubmit, dirty}) => (

                <form className="login" onSubmit={handleSubmit}>
                        <img src={images.logo} alt=""/>
                        <input className='loginput' name={`email`} type={`text`} placeholder="email"
                               onChange={handleChange} onBlur={handleBlur} value={values.email}/>
                        {touched.email && errors.email && <p style={{color: 'red'}}>{errors.email}</p>}
                        <br/><br/>
                        <input name={`password`} className='loginput' type={`password`} placeholder="password"
                               onBlur={handleBlur} onChange={handleChange} value={values.password}/>
                        {touched.password && errors.password && <p style={{color: 'red'}}>{errors.password}</p>}
                        <br/><br/>
                        <button className='logBtn' type={'submit'}>Log In</button>
                        <button type='button' className='logBtn'>Sign Up</button>
                </form>

            )}
        </Formik>
    )
}

export default Login