import {createSlice} from "@reduxjs/toolkit";

const messengerSlices = createSlice({
    name: "messenger",
    initialState: {
        allMessages: [],
        senderUserID: null,
        getterUserId: null
    },
    reducers: {
        getterUserID(state, {payload}) {
            console.log("test1")
            state.getterUserId = payload
        },

        currentUserID(state, {payload}) {
            console.log("test2")
            state.senderUserID = payload
        },

        sendMessage(state, {payload}) {
            console.log("test3")
            return {
                ...state,
                allMessages: [
                    ...state.allMessages,
                    {
                        fromId: state.senderUserID,
                        toId: state.getterUserId,
                        message: payload,
                    }
                ]
            }
        }
    },
    extraReducers: {}
})

export const selectMessage = state => state.messenger

export const {sendMessage, currentUserID, getterUserID} = messengerSlices.actions

export const messengerReducer = messengerSlices.reducer