import axios from "axios";
import {createAsyncThunk} from "@reduxjs/toolkit";

export const fetchPosts = createAsyncThunk(
    "posts/fetchPosts",
    async function () {
        const {data: postsData} = await axios.get("https://jsonplaceholder.typicode.com/photos?_limit=500")
        const {data: commentsData} = await axios.get("https://jsonplaceholder.typicode.com/comments")
        const data = postsData.map(post => ({
            id: post.id.toString(),
            name: post.title.split(" ")[0],
            likesCount: Math.round(Math.random() * 100 + 300),
            timeAgo: Math.round(Math.random() * 3 + 4) + " Minutes Ago",
            postText: post.title.split(" ").slice(1).join(" "),
            img: post.url,
            comments: [
                ...commentsData.filter(comment => comment.postId === post.id)
                    .map(comment => ({
                        id: comment.id.toString(),
                        username: comment.name.split(" ")[0],
                        body: comment.body
                    }))
            ]
        }))

        return data

    }
)