
import "./Registration.css"

function Registration() {
    return (
        <div className="reg">
            <h1 className="regtitle">Registration</h1>
            <form>
                <input className="regInput" type="text" name='name' placeholder='Name'/><br/> <br/>
                <input className="regInput" type="text" name='email' placeholder='Email'/><br/> <br/>
                <input className="regInput" type="text" name='password' placeholder='Password'/> <br/> <br/>
                <button className="regBtn">Add account</button>
            </form>
        </div>
    )
}

export default Registration