import React, {useEffect, useMemo} from 'react'
import IMAGES from '../../images'
import Post from '../Post/Post'
import {useDispatch, useSelector} from "react-redux";
import {selectPosts} from "../../store/slices/Posts/postsSlices";
import {fetchPosts} from "../../store/slices/Posts/postsAPI";
import LoadingSpinner from "../LoadingSpinner/LoadingSpinner";
import {resetSearch, selectSearch} from "../../store/slices/Search/searchSlice";

function Posts() {

    const dispatch = useDispatch()
    const {postsData: posts, isLoading: loaded} = useSelector(selectPosts)
    const search = useSelector(selectSearch)
    useEffect(() => {
        if (!posts.length) {
            dispatch(fetchPosts())
        }
        return () => {
            dispatch(resetSearch())
        }
    }, [])

    // const posts = [
    //     {
    //         id: '1',
    //         img: IMAGES.cover1,
    //         name: 'user1',
    //         likesCount: '1,200',
    //         postText: 'Lorem ipsum dolor sit amet consectetur, adipisicing elit. Pariatur tenetur veritatis placeat, molestiae impedit aut provident eum quo natus molestias?',
    //         timeAgo: '2 Minutes Ago'
    //
    //     },
    //     {
    //         id: '2',
    //         img: IMAGES.cover2,
    //         name: 'user2',
    //         likesCount: '1,200',
    //         postText: 'Lorem ipsum dolor sit amet consectetur, adipisicing elit. Pariatur tenetur veritatis placeat, molestiae impedit aut provident eum quo natus molestias?',
    //         timeAgo: '2 Minutes Ago'
    //     },
    //     {
    //         id: '3',
    //         img: IMAGES.cover3,
    //         name: 'user3',
    //         likesCount: '1,200',
    //         postText: 'Lorem ipsum dolor sit amet consectetur, adipisicing elit. Pariatur tenetur veritatis placeat, molestiae impedit aut provident eum quo natus molestias?',
    //         timeAgo: '2 Minutes Ago'
    //     },
    //     {
    //         id: '4',
    //         img: IMAGES.cover4,
    //         name: 'user4',
    //         likesCount: '1,200',
    //         postText: 'Lorem ipsum dolor sit amet consectetur, adipisicing elit. Pariatur tenetur veritatis placeat, molestiae impedit aut provident eum quo natus molestias?',
    //         timeAgo: '2 Minutes Ago'
    //     }
    // ]]\


    const filteredPosts = useMemo(() => {
        return [...posts.filter(posts => posts.name.includes(search))]
    }, [search, posts])

    return (

        <>
            {
                loaded ? <LoadingSpinner/> :
                filteredPosts?.map(el => <Post key={el.id} id={el.id} img={el.img} comments={el.comments} name={el.name}
                                               likesCount={el.likesCount} postText={el.postText} timeAgo={el.timeAgo}/>)
            }
        </>
    )
}

export default Posts